/* variables */

:root {
    --shade-gold: #988850;
}

/* fonts */

/* Noto Sans */

@font-face {
    font-family: "Noto Sans";
    src: url("fonts/NotoSans/NotoSans-Regular.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Noto Sans";
    src: url("fonts/NotoSans/NotoSans-Bold.ttf") format("truetype");
    font-weight: bold;
    font-style: normal;
}

/* Mirza */

@font-face {
    font-family: "Mirza";
    src: url("fonts/Mirza/Mirza-Regular.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Mirza";
    src: url("fonts/Mirza/Mirza-Bold.ttf") format("truetype");
    font-weight: bold;
    font-style: normal;
}

/* styles */

.App {}

.page {}

/* header */

.header-wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    position: fixed;
    z-index: 10;
}

.header-icon {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 140px;
    margin: 50px 0px 0px 50px;
    font-family: "Noto Sans";
    font-size: 36px;
    color: #FFFFFF;
    user-select: none;
    z-index: 1000;
}

.header-icon > img {
    width: 40px;
    height: 45px;
}

/* menu assets */

.menu-icon {
    position: relative;
    margin: 50px 50px 0px 0px;
    user-select: none;
    z-index: 1000;
    transition: transform 0.3s ease-in-out;
}

.menu-icon-span {
    position: absolute;
    background-color: white;
    transition: all 0.3s ease-in-out;
}

.menu-panel {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background-color: rgb(15, 15, 15);
    transform: translateX(100%);
    transition: transform 0.7s ease-in-out;
}

.menu-panel.active {
    transform: translateX(0);
}

.menu-items-wrapper {
    margin: 0px 0px 0px 45px;
    display: flex;
    flex-direction: column;
    gap: 100px;
}

.menu-item-container {
    cursor: pointer;
    text-decoration: none;
    transition: transform 0.3s ease-in-out;
}

.menu-item-text {
    font-family: "Noto Sans";
    font-size: 72px;
    color: white;
}

/* homepage assets */

.homepage-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    /* height: 5000px; */
    position: relative;
}

.homepage-blackbkg {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: -100;
    background-color: black;
}

.homepage-background {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -100;
    background: url(images/background.png) no-repeat left bottom fixed;
    background-size: cover;
}

.homepage-intro-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.homepage-fade-bkg {
    opacity: 0;
    transition:
        opacity 0.7s ease;
}

.homepage-fade-bkg.visible {
    opacity: 1
}

.homepage-titlediv {
    display: flex;
    flex-direction: column;
    user-select: none;
    position: fixed;
}

.homepage-title {
    font-family: "Noto Sans";
    font-weight: bold;
    font-size: 288px;
    color: #FFFFFF;
    text-shadow:
        5px -5px 1px violet,
        -5px 5px 3px blue;
}

.homepage-subtitle {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: -50px 0px 0px 0px;
    font-family: "Mirza";
    font-size: 24px;
    color: var(--shade-gold);
}

.homepage-subtitle > hr:nth-child(1) {
    width: 72px;
    height: 3px;
    margin: 10px 10px 0px 0px;
    background-color: var(--shade-gold);
    border: none;
}

.homepage-subtitle > hr:nth-child(2) {
    width: 72px;
    height: 3px;
    margin: 10px 0px 0px 10px;
    background-color: var(--shade-gold);
    border: none;
}

.homepage-fade {
    opacity: 0;
    transform: translateY(20px);
    transition:
        opacity 0.7s ease,
        transform 0.7s ease;
}

.homepage-fade.visible {
    opacity: 1;
    transform: translateY(0);
}